.footer {
    width: 100%;
    background-color: black;
    color: #FFFFFF;
    text-align: center;
    margin-top: 6.25rem;
    padding: 60px 0;
    @media screen and (min-width: 767.99px){
        padding: 63px 0 30px;
    }


    &__logo {
        width: 7.6rem;
        filter: saturate(0%) brightness(0%) invert(100%);
    }

    &__agree {
        font-weight: 500;
        font-size: 12px;
        padding-top: 30px;
        margin: 0;
        @media screen and (min-width: 767.99px) {
            font-size: 24px;
    }

}
}