.banner {
    position: relative;
    @include flex-center;
    align-items: center;
    flex-shrink: 0;
    background-color: white;
    border-radius: 1.25rem;


    &__text {
        position: absolute;
        font-size: 1.875rem;
        color: white;
        font-weight: 500;

        @media (min-width: 374.99px) and (max-width :768px) {
        font-size: 0.875rem;
        }
        @media (max-width: 425px) 
{
width: 132px;
margin-top: 54px;
left:3%
}  
    }

    &__image {
        width: calc(100% - 12.5rem);
        height: 13.938rem;
        object-fit: cover;
        -webkit-filter: brightness(20%);
        filter: brightness(90%);
        border-radius: 25px;
        margin-bottom: 10px;
        margin-top: 25px;
        @media screen and (max-width: 425px) {
            margin-top: 16px;
    }
}
}
/*************************/


@media (min-width: 350px) and (max-width: 539.99px) {


        .banner{
            margin-left: 10px;
     
        &__image{
            width: 100%;
            height: 111px;
            object-position: bottom 2px right -7px;
            margin-bottom: -25px;
            border-radius: 10px;
        }

    
     
    }}
