.nav {
    width: calc(100% - 12rem);
    @include flex-between;
    align-items: flex-start;
    margin: auto;

    @media screen and (max-width: 425px) {
        width: 100%;
        margin-left: 12px;
        font-size: 14px;
        align-items: inherit;
    }

    &__logo {
        margin-top: 15px;
        width: 145px;
        height: 47px;
        margin-bottom: 1rem;
        @media screen and (max-width: 767.99px) {
            width: 9.1rem;
            height: 2.938rem;
            margin-top: 22px;
        }
        @media screen and (max-width: 425px) {
            width: 6.06rem;
            height: 1.8rem;
            margin-bottom: -4rem;
        }
    }
    ul {
        @include flex-end;
        margin: 1.875rem 2.5rem 0rem 0rem;
        align-items: flex-end;

        a {
            text-decoration: none;
            padding: 5px;
            width: 7.5rem;

            li {
                @include flex-end;
                font-size: 1.5rem;
                width: 7.5rem;
                font-weight: 500;
                list-style: none;
                color: $color-1;
                transition: 0.2s;
                cursor: pointer;

                &:hover {
                    color: $color-1;
                }
            }

            .nav__about {
                width: 9.8rem;
                @media screen and  (max-width: 375px) {
                    width: 8.2rem;
                    font-size: 14px;
                }
                @media screen and  (min-width: 425px) {
                    width: 9.2rem;
                    font-size: 14px;
            }
        }
            .nav__home {
                @media screen and (max-width: 425px) {
                    font-size: 14px;
                }
            }

            //*barre en dessous de la nav
            .nav-active {
                text-decoration: underline;
                color: $color-1;
            }
        }
    }
}
